import React from "react";
import "./style/contact.css";

function Contact() {
  return (
    <div
      id="contact"
      style={{
        width: "100%",
        backgroundColor: "#d2d9e2",
        display: "flex",
        justifyContent: "space-evenly",
        alignItems: "center",
        padding: "70px 20px 20px 20px",
        position: "relative",
      }}
    >
      <h3
        style={{
          position: "absolute",
          top: "17px",
          fontWeight: "500",
          fontSize: "22px",
        }}
      >
        <u>Contact Me</u>{" "}
      </h3>
      <div className="mailing">
        <form
          action="https://formsubmit.co/koley.sourav.work@gmail.com"
          method="POST"
        >
          <input type="text" name="name" placeholder="Your name" required />
          <input
            type="email"
            name="email"
            placeholder="Your mail address"
            required
          />
          <textarea
            name="message"
            placeholder="Write your content here"
            required
          ></textarea>
          <button type="submit">Send</button>
        </form>
      </div>
      <div className="links">
        <div>
          <div id="github"></div>
          <a
            href="https://github.com/kolarav/"
            rel="noreferrer"
            target="_blank"
          >
            Github
          </a>
        </div>
        <div>
          <div id="linkedin"></div>
          <a
            href="https://www.linkedin.com/in/sourav-koley-b27995211/"
            rel="noreferrer"
            target="_blank"
          >
            Linkedin
          </a>
        </div>
        <div>
          <div id="codepen"></div>{" "}
          <a
            href="https://codepen.io/koley717"
            rel="noreferrer"
            target="_blank"
          >
            Codepen
          </a>
        </div>
      </div>
    </div>
  );
}

export default Contact;
